.filter {
  margin: 0px 0px 10px 0;
  height: 25px;
}
.keywordLabel {
  margin-left: 30px;
  font-size: 12px;
  letter-spacing: 1px;
}
.keywordInput {
  width: 120px;
  /* margin-left: 15px; */
  height: 25px;
  border-radius: 2px;
  outline: 0px;
  border: 1px solid #CCC;
  padding-left: 3px;
  font-size: 12px;
  letter-spacing: 1px;
}
.keywordButton {
  margin-left: 15px;
  width: 75px;
  height: 25px;
  line-height: 25px;
  padding-top: 0;
  font-size: 12px;
  letter-spacing: 2px;
  border-radius: 2px !important;
  margin-top: -3px;
}



