.noneSelectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.mask {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0;
  z-index: 1054;
  transition: 300mx;
  -moz-transition: 300mx;
  -ms-transition: 300mx;
  -webkit-transition: 300mx;
  -o-transition: 300mx;
}
.mask.show {
  opacity: 0.3;
}



