.geoFilter .province-selector,
.geoFilter .city-selector,
.geoFilter .store-selector {
  width: 75px;
  margin-left: 15px;
  margin-bottom: 10px;
  font-size: 12px;
  padding-left: 3px;
  height: 25px;
  border-radius: 2px;
  letter-spacing: 1px;
  border: 1px solid #CCC;
}
.geoFilter .province-selector .option,
.geoFilter .city-selector .option,
.geoFilter .store-selector .option {
  letter-spacing: 1px !important;
}



