.noneSelectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.main {
  position: absolute;
  width: 100% !important;
  height: 100%;
  background-color: #f9f9f9;
  font-size: 12px;
  transition: 300ms;
  -moz-transition: 300ms;
  -ms-transition: 300ms;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
}
.main.showMenu {
  left: 200px;
}
.main.notShowMenu {
  left: 0px;
}
.main .mainTablePane {
  position: absolute;
  top: 35px;
  left: 0px;
  height: calc(100% - 40px);
  width: calc(100% - 200px) !important;
}
.main .mainTablePane .briefMainTablePane {
  width: 250px;
  height: 100%;
  float: left;
}
.main .mainTablePane .detailMainTablePane {
  width: calc(100% - 250px);
  height: 100%;
  float: right;
}
.main .mainTablePane .detailMainTablePane .closer {
  position: absolute;
  right: 8px;
  height: 8px;
  font-size: 20px;
  cursor: pointer;
}
.main .mainTablePane .separater {
  position: absolute;
  height: 100%;
  left: 250px;
  background-color: black;
  width: 2px;
  opacity: 0.1;
}
.main .rightPane {
  background-color: #FBEFE5;
  color: #333;
  outline: 2px solid #EEE;
  position: absolute;
  height: 100%;
  width: 200px;
  top: 0px;
  right: 0px;
  font-size: 13px;
}
.menuTrigger {
  position: absolute;
  top: 10px;
  left: 15px;
  font-size: 12px;
  cursor: pointer;
}
.modal-footer {
  padding-right: 20px;
}
.modal-title {
  font-size: 16px;
  /* font-weight: bold */
}
.transparent {
  opacity: 0;
}



