.noneSelectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.tableBodyPane {
  overflow: auto;
  height: 40%;
  border: 1px solid #EEE;
}
.tableFunctionPane {
  margin: 15px 0 10px 0;
  padding: 30px 0;
  border-top: 2px solid #F5F5F5;
  border-bottom: 2px solid #F5F5F5;
  height: calc(60% - 110px);
}
.tableFunctionPane i {
  padding: 0 10px 0 7px;
}
.mainTable {
  width: 100%;
}
.mainTable thead {
  font-size: 14px;
  font-weight: bold;
  border: 1px solid #EEE;
}
.mainTable thead th {
  white-space: nowrap;
  padding-right: 15px !important;
  padding-left: 15px !important;
}
.mainTable tbody {
  border: 1px solid #EEE;
}
.mainTable tbody tr:nth-of-type(odd) {
  background-color: #f9f9f9;
}
.mainTable tbody tr:nth-of-type(even) {
  background-color: #FBEFE5;
}
.mainTable tbody tr:hover {
  background-color: #F7E1CE;
}
.mainTable tbody td {
  cursor: pointer;
  font-size: 12px;
  white-space: nowrap;
  padding-right: 15px !important;
  padding-left: 15px !important;
}
.mainTable img {
  max-height: 60px;
}
.tableName {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
  padding-left: 16px;
  border-top: 2px solid #F5F5F5;
}
.tableHelper {
  margin-bottom: 10px;
}
.tableFunctionButton {
  font-size: 13px;
  cursor: pointer;
}
.tableFunctionButton:hover {
  color: #C06016;
}
.tableFunctionButton.disabled {
  color: #888;
  cursor: default;
}
.tableFooter {
  text-align: right;
  width: 100%;
  margin-top: 10px;
  color: #888;
  font-size: 12px;
  padding: 0 15px;
}
.tableFooter ul {
  float: right;
}
.tableFooter li {
  list-style: none;
  float: left;
}
.tableFooter button {
  height: 16px;
  line-height: 16px;
  padding: 0;
  margin: 0 2px;
  width: 12px;
  border: 0;
  border-bottom: 1px solid #CCC;
  background-color: #f9f9f9;
}
.tableFooter button.theone {
  border-bottom: 1px solid #E08031;
  color: #555;
}



