.noneSelectable,
.btn {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
html,
body,
.root {
  height: 100%;
  width: 100%;
  font-family: "微软雅黑";
  color: #333;
  padding: 0px;
  margin: 0px;
  overflow: hidden;
  letter-spacing: 1px;
  background-color: #DDD;
  text-align: left;
}
.btn {
  background-color: #E08031 !important;
  border: #D97420;
  border-radius: 2px;
  font-size: 13px;
  margin-right: 15px;
}
.btn-default {
  background-color: #E08031 !important;
  border: #D97420;
  color: white;
  outline: 0;
}
.btn-default:hover {
  background-color: #C06016 !important;
  color: white;
}
li {
  list-style: none;
}
.no-border {
  border: 0;
}
.loading {
  position: fixed;
  font-size: 50px;
  opacity: 1;
  color: #C06016;
  left: calc(50% - 25px);
  top: calc(50% - 25px);
}
.empty:after,
.important:after {
  content: " *";
  color: red;
  font-weight: bold;
}
.mask {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0;
  z-index: 1054;
  transition: 300ms;
  -moz-transition: 300ms;
  -ms-transition: 300ms;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
}
.mask.show {
  opacity: 0.3;
}
.importModal .modal-dialog {
  width: 80%;
}
.importTablePane {
  overflow-x: auto;
}
.importTable td,
.importTable th {
  white-space: nowrap;
  padding-right: 15px !important;
  padding-left: 15px !important;
}
.importTable th {
  font-size: 14px;
  font-weight: bold;
}
.importTable td {
  font-size: 12px;
}
.importTable caption {
  font-size: 14px;
}
.modalTableTotal {
  font-size: 12px;
}
.importModalResult {
  font-size: 12px;
  margin: 15px;
}
.importModalResultHeader {
  font-size: 13px;
  font-weight: bold;
}
.managerInput {
  margin-left: 100px !important;
  height: 18px;
  font-size: 18px;
  cursor: pointer;
}
.modal-footer {
  padding-right: 20px;
}
.modal-title {
  font-size: 16px;
  /* font-weight: bold */
}
.modal-body label,
.modal-body input {
  font-size: 13px;
}
.imagePreviewWrapper {
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
  margin-top: 15px;
  margin-bottom: 15px;
}
.previewImage {
  width: 100%;
}



