.noneSelectable,
.btn {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
html,
body,
.root {
  height: 100%;
  width: 100%;
  font-family: "微软雅黑";
  color: #333;
  padding: 0px;
  margin: 0px;
  overflow: hidden;
  letter-spacing: 1px;
  background-color: #DDD;
  text-align: left;
}
.btn {
  background-color: #E08031 !important;
  border: #D97420;
  border-radius: 2px;
  font-size: 13px;
  margin-right: 15px;
}
.btn-default {
  background-color: #E08031 !important;
  border: #D97420;
  color: white;
  outline: 0;
}
.btn-default:hover {
  background-color: #C06016 !important;
  color: white;
}
li {
  list-style: none;
}
.no-border {
  border: 0;
}
.loading {
  position: fixed;
  font-size: 50px;
  opacity: 1;
  color: #C06016;
  left: calc(50% - 25px);
  top: calc(50% - 25px);
}
.empty:after,
.important:after {
  content: " *";
  color: red;
  font-weight: bold;
}
.mask {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0;
  z-index: 1054;
  transition: 300ms;
  -moz-transition: 300ms;
  -ms-transition: 300ms;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
}
.mask.show {
  opacity: 0.3;
}



