.modal-dialog {
  width: 80%;
}
.disabled {
  color: grey;
}
.skuTip {
  position: absolute;
}
.skuTip .skuTipList {
  position: absolute;
  padding: 0;
  border: 1px #888 solid;
  background-color: white;
}
.skuTip .skuTipList .skuTipItem {
  padding: 0px 20px 0 10px;
  width: 100%;
}
.skuTip .skuTipList .skuTipItem:hover {
  background-color: lightblue;
  cursor: pointer;
}
.addPromotionItem {
  padding-left: 15px !important;
}
.addPromotionItem:hover {
  background-color: #DDD;
  cursor: pointer;
}
.deletePromotionItem {
  padding-left: 15px !important;
}
.deletePromotionItem:hover {
  background-color: #DDD;
  cursor: pointer;
}
tr.errorRow {
  background-color: lightcoral !important;
}



