.noneSelectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.editorPane {
  border: 1px solid #EEE;
  height: calc(100% - 20px);
  width: 100%;
  overflow-y: auto;
  padding-left: 20px;
  padding-bottom: 20px;
  float: left;
}
.editorTitle {
  padding: 10px 0 5px 5px;
  font-size: 13px;
  font-weight: bold;
}
.editorList {
  padding-left: 5px;
  margin-top: 10px;
}
.editorLabel {
  font-size: 12px;
  font-weight: normal;
  width: 100px;
}
.editorInput {
  font-size: 12px;
  border: 1px solid #EEE;
  border-radius: 2px;
  border-color: #E08031;
  padding-left: 3px;
  letter-spacing: 1px;
  background-color: #f9f9f9;
  width: 200px;
}
.editorElement {
  border-bottom: 1px solid #F5F5F5;
}
.editorInput[type="checkbox"] {
  width: 16px;
  height: 16px;
}
.editorInput.disabled {
  outline: 0;
  border: 0;
  border-bottom: 1px solid #CCC;
}



